<template>
  <form id="quote-creation">
    <div class="accordion" id="accordionExample">
      <div class="accordion-item my-blue">
        <h2 class="accordion-header my-blue" id="headingOne">
          <button
            class="accordion-button"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseOne"
            aria-expanded="true"
            aria-controls="collapseOne"
          >
            <svg
              width="25"
              height="24"
              viewBox="0 0 25 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M3.7999 0.400024C3.03077 0.400024 2.29315 0.705559 1.74929 1.24941C1.20544 1.79327 0.899902 2.5309 0.899902 3.30002V19.25C0.899902 20.4037 1.3582 21.5102 2.17399 22.3259C2.98977 23.1417 4.09621 23.6 5.2499 23.6C6.40359 23.6 7.51003 23.1417 8.32582 22.3259C9.1416 21.5102 9.5999 20.4037 9.5999 19.25V3.30002C9.5999 2.5309 9.29437 1.79327 8.75051 1.24941C8.20666 0.705559 7.46903 0.400024 6.6999 0.400024H3.7999ZM5.2499 20.7C5.63447 20.7 6.00328 20.5473 6.27521 20.2753C6.54713 20.0034 6.6999 19.6346 6.6999 19.25C6.6999 18.8655 6.54713 18.4966 6.27521 18.2247C6.00328 17.9528 5.63447 17.8 5.2499 17.8C4.86534 17.8 4.49653 17.9528 4.2246 18.2247C3.95267 18.4966 3.7999 18.8655 3.7999 19.25C3.7999 19.6346 3.95267 20.0034 4.2246 20.2753C4.49653 20.5473 4.86534 20.7 5.2499 20.7ZM12.4999 18.1524L19.6049 11.0474C20.1486 10.5035 20.454 9.76605 20.454 8.99707C20.454 8.2281 20.1486 7.4906 19.6049 6.94677L17.5532 4.89502C17.0093 4.35136 16.2718 4.04594 15.5029 4.04594C14.7339 4.04594 13.9964 4.35136 13.4526 4.89502L12.4999 5.84767V18.1524ZM21.1999 23.6H11.1529L19.8529 14.9H21.1999C21.969 14.9 22.7067 15.2056 23.2505 15.7494C23.7944 16.2933 24.0999 17.0309 24.0999 17.8V20.7C24.0999 21.4692 23.7944 22.2068 23.2505 22.7506C22.7067 23.2945 21.969 23.6 21.1999 23.6V23.6Z"
                fill="white"
              />
            </svg>
            Nastavenia cenovej ponuky
          </button>
        </h2>
        <div
          id="collapseOne"
          class="accordion-collapse collapse show"
          aria-labelledby="headingOne"
          data-bs-parent="#accordionExample"
        >
          <div class="accordion-body">
            <div class="my-container quote-settings">
              <div class="quote-side left">
                <div class="input-box">
                  <label>
                    INTERnÝ NÁZOV PONUKY
                    <input v-model="internalQuoteName" />
                  </label>
                  <img
                    :src="i"
                    class="i-icon"
                    data-bs-toggle="tooltip"
                    data-bs-placement="top"
                    title="tooltip on top"
                  />
                </div>

                <div class="input-box">
                  <label>
                    Interné ID ponuky
                    <input v-model="internalQuoteId" />
                  </label>
                  <img
                    :src="i"
                    class="i-icon"
                    data-bs-toggle="tooltip"
                    data-bs-placement="top"
                    title="tooltip on top"
                  />
                </div>

                <div class="quote-selects-flex">
                  <div>
                    <div class="form-floating mb-3">
                      <Multiselect
                        v-model="language"
                        :options="languageOptions"
                        label="label"
                        value="label"
                        selectLabel=""
                        selectedLabel=""
                        deselectLabel=""
                        deselectGroupLabel=""
                        selectGroupLabel=""
                        placeholder="Jazyk ponuky"
                        track-by="value"
                        :allowEmpty="false"
                        :show-labels="false"
                      >
                        <template slot="singleLabel" slot-scope="props">
                          <img
                            class="option__image"
                            v-if="props.option.flag"
                            :src="
                              require(`/src/assets/svg/flags/${props.option.flag}`)
                            "
                            :alt="props.option.label"
                          />
                          <span class="option__label">{{
                            props.option.label
                          }}</span>
                        </template>
                        <template slot="option" slot-scope="props">
                          <img
                            class="option__image"
                            v-if="props.option.flag"
                            :src="
                              require(`/src/assets/svg/flags/${props.option.flag}`)
                            "
                            :alt="props.option.label"
                          />
                          <span class="option__label">{{
                            props.option.label
                          }}</span>
                        </template>
                        <span slot="noResult">
                          Oops! No elements found. Consider changing the search
                          query.
                        </span>
                      </Multiselect>

                      <label for="floatingInput" style="top: -13px">
                        Jazyk ponuky
                      </label>
                    </div>
                  </div>
                  <div>
                    <div class="form-floating mb-3">
                      <Multiselect
                        v-model="currency"
                        :options="currencyOptions"
                        placeholder="Mena"
                        selectLabel=""
                        selectedLabel=""
                        deselectLabel=""
                        deselectGroupLabel=""
                        selectGroupLabel=""
                        :allowEmpty="false"
                      >
                        <span slot="noResult">
                          Oops! No elements found. Consider changing the search
                          query.
                        </span>
                      </Multiselect>
                      <label for="floatingInput" style="top: -13px">
                        Mena
                      </label>
                    </div>
                  </div>
                </div>

                <div class="input-box" style="margin-top: 15px">
                  <label>
                    Názov klienta
                    <input
                      v-model="nameOfClient"
                      @keydown.prevent.enter
                      @keydown.prevent.esc="nameOfClient = ''"
                    />
                  </label>
                  <img :src="searchIcon" class="i-icon" style="cursor: text" />
                  <div class="name-options" v-if="nameOfClient">
                    <div
                      v-if="filteredClients.length > 0"
                      style="max-height: 200px"
                    >
                      <div
                        v-for="client in filteredClients"
                        class="single-name-client"
                        :key="client"
                        @click="selectNameClient(client)"
                      >
                        {{ client }}
                      </div>
                    </div>
                    <div
                      class="no-client-found"
                      v-else-if="!clients.includes(nameOfClient)"
                    >
                      Nenašiel sa žiadny klient.
                    </div>
                    <div
                      data-v-fdf65310=""
                      class="bottom"
                      v-if="!clients.includes(nameOfClient)"
                      @click="addClient(nameOfClient)"
                    >
                      + Pridať klienta s názvom "{{ nameOfClient }}"
                    </div>
                  </div>
                </div>

                <div class="client-boxes">
                  <div
                    v-for="client in clients"
                    :key="client"
                    class="client-box"
                    @click="nameOfClient = client"
                  >
                    {{ client }}
                  </div>
                </div>

                <div class="input-box">
                  <label>
                    Platnosť ponuky
                    <input v-model="validityDate" type="date" />
                  </label>
                  <div class="i-icon no-circle">
                    <svg
                      width="14"
                      height="13"
                      viewBox="0 0 14 13"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M4.88889 7.27783H3.77778C3.59444 7.27783 3.44444 7.14658 3.44444 6.98617V6.01394C3.44444 5.85353 3.59444 5.72228 3.77778 5.72228H4.88889C5.07222 5.72228 5.22222 5.85353 5.22222 6.01394V6.98617C5.22222 7.14658 5.07222 7.27783 4.88889 7.27783ZM7.88889 6.98617V6.01394C7.88889 5.85353 7.73889 5.72228 7.55556 5.72228H6.44444C6.26111 5.72228 6.11111 5.85353 6.11111 6.01394V6.98617C6.11111 7.14658 6.26111 7.27783 6.44444 7.27783H7.55556C7.73889 7.27783 7.88889 7.14658 7.88889 6.98617ZM10.5556 6.98617V6.01394C10.5556 5.85353 10.4056 5.72228 10.2222 5.72228H9.11111C8.92778 5.72228 8.77778 5.85353 8.77778 6.01394V6.98617C8.77778 7.14658 8.92778 7.27783 9.11111 7.27783H10.2222C10.4056 7.27783 10.5556 7.14658 10.5556 6.98617ZM7.88889 9.3195V8.34728C7.88889 8.18686 7.73889 8.05561 7.55556 8.05561H6.44444C6.26111 8.05561 6.11111 8.18686 6.11111 8.34728V9.3195C6.11111 9.47992 6.26111 9.61117 6.44444 9.61117H7.55556C7.73889 9.61117 7.88889 9.47992 7.88889 9.3195ZM5.22222 9.3195V8.34728C5.22222 8.18686 5.07222 8.05561 4.88889 8.05561H3.77778C3.59444 8.05561 3.44444 8.18686 3.44444 8.34728V9.3195C3.44444 9.47992 3.59444 9.61117 3.77778 9.61117H4.88889C5.07222 9.61117 5.22222 9.47992 5.22222 9.3195ZM10.5556 9.3195V8.34728C10.5556 8.18686 10.4056 8.05561 10.2222 8.05561H9.11111C8.92778 8.05561 8.77778 8.18686 8.77778 8.34728V9.3195C8.77778 9.47992 8.92778 9.61117 9.11111 9.61117H10.2222C10.4056 9.61117 10.5556 9.47992 10.5556 9.3195ZM13.2222 3.00005V11.5556C13.2222 12.1997 12.625 12.7223 11.8889 12.7223H2.11111C1.375 12.7223 0.777778 12.1997 0.777778 11.5556V3.00005C0.777778 2.35596 1.375 1.83339 2.11111 1.83339H3.44444V0.569499C3.44444 0.409082 3.59444 0.277832 3.77778 0.277832H4.88889C5.07222 0.277832 5.22222 0.409082 5.22222 0.569499V1.83339H8.77778V0.569499C8.77778 0.409082 8.92778 0.277832 9.11111 0.277832H10.2222C10.4056 0.277832 10.5556 0.409082 10.5556 0.569499V1.83339H11.8889C12.625 1.83339 13.2222 2.35596 13.2222 3.00005ZM11.8889 11.4098V4.16672H2.11111V11.4098C2.11111 11.49 2.18611 11.5556 2.27778 11.5556H11.7222C11.8139 11.5556 11.8889 11.49 11.8889 11.4098Z"
                        fill="#9598A7"
                      />
                    </svg>
                  </div>
                </div>

                <div class="toggles">
                  <div class="toggle-with-tooltip">
                    <Toggle
                      title="Možnosť prijať alebo odmietnuť cenovú ponuku"
                      :value="approveDeclinePriceQuote"
                      :update="(value) => (approveDeclinePriceQuote = value)"
                      :stacked="true"
                    />
                    <img
                      :src="i"
                      class="i-icon"
                      data-bs-toggle="tooltip"
                      data-bs-placement="top"
                      title="tooltip on top"
                    />
                  </div>

                  <div class="toggle-with-tooltip">
                    <Toggle
                      title="Recenzie od mojich klientov"
                      :value="showReviews"
                      :update="(value) => (showReviews = value)"
                      :stacked="true"
                    />
                    <img
                      :src="i"
                      class="i-icon"
                      data-bs-toggle="tooltip"
                      data-bs-placement="top"
                      title="tooltip on top"
                    />
                  </div>

                  <div class="toggle-with-tooltip">
                    <Toggle
                      title="Pridať dobu dodania"
                      :value="addDeliveryDate"
                      :update="(value) => (addDeliveryDate = value)"
                      :stacked="true"
                    />
                    <img
                      :src="i"
                      class="i-icon"
                      data-bs-toggle="tooltip"
                      data-bs-placement="top"
                      title="tooltip on top"
                    />
                  </div>

                  <div v-if="addDeliveryDate">
                    <input
                      class="input-welcome-text common-border-shadow delivery-date title"
                      placeholder="DOBA DODANIA"
                      v-model="deliveryDateTitle"
                    />
                    <textarea
                      class="input-welcome-text common-border-shadow delivery-date text"
                      placeholder="TEXT"
                      v-model="deliveryDateText"
                    />
                  </div>
                </div>
              </div>

              <div class="quote-side right">
                <h6 class="quote-title">Vzhľad a farebná kombinácia</h6>
                <h6 class="quote-subtitle">
                  Vzhľad cenovej ponuky môžeš zmeniť aj neskôr.
                </h6>

                <div class="colors">
                  <div
                    v-for="myColor in defaultColors"
                    :key="myColor"
                    @click="color = myColor"
                    :class="`color-box ${myColor === color ? 'selected' : ''}`"
                  >
                    <div class="inner" :style="`background: ${myColor}`"></div>
                  </div>
                </div>

                <div
                  :class="`custom-color-button ${
                    defaultColors.filter((col) => col === color).length === 0
                      ? 'selected'
                      : ''
                  }`"
                  @click="clickCustomColor"
                >
                  Vlastná farba
                  <input
                    class="color-input"
                    type="color"
                    v-model="color"
                    v-if="
                      defaultColors.filter((col) => col === color).length === 0
                    "
                  />
                </div>

                <div
                  class="input-box"
                  style="margin-top: 20px"
                  v-if="
                    defaultColors.filter((col) => col === color).length === 0
                  "
                >
                  <label>
                    HEX kód farby
                    <input v-model="color" />
                  </label>
                </div>

                <div class="hide-logo-toggle-container">
                  <Toggle
                    title="Skryť logo DOFFERO"
                    :value="hideDofferoLogo"
                    :update="(value) => (hideDofferoLogo = value)"
                  />
                  <img
                    :src="i"
                    class="i-icon"
                    style="margin-left: 10px; margin-top: -2px"
                    data-bs-toggle="tooltip"
                    data-bs-placement="top"
                    title="tooltip on top"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="my-container common-border-shadow template-use">
      <div class="select-template-buttons">
        <div style="text-align: left">
          <p class="template-use-title">
            Nahraj si položky z dopredu vytvorenej šablóny
          </p>
          <p class="template-use-desc">
            Uľahči si prácu a vytvor si šablóny ponúk, ktoré vytváraš
            najčastejšie.
          </p>
        </div>
        <div
          class="green-button"
          @click="showTemplateSelect = !showTemplateSelect"
        >
          Vybrať šablónu
        </div>
      </div>
      <div
        class="form-floating type-item template-use-select"
        v-if="showTemplateSelect"
      >
        <Multiselect
          v-model="templateSelect"
          :options="selectOptions"
          placeholder=""
          selectLabel=""
          selectedLabel=""
          deselectLabel=""
          deselectGroupLabel=""
          selectGroupLabel=""
          :allowEmpty="false"
        >
          <span slot="noResult">
            Oops! No elements found. Consider changing the search query.
          </span>
        </Multiselect>
        <label for="floatingInput" style="top: -13px"> Šablóna </label>
      </div>
    </div>

    <div class="my-container common-border-shadow">
      <div class="top-inputs">
        <div class="logo-input-container common-border-shadow">
          <img :src="logo" />
          <label class="logo-upload-text">
            <span> Zmeniť logo </span>
            <input
              class="logo-input"
              type="file"
              placeholder="Change logo"
              @change="logoInput"
            />
          </label>
        </div>
        <div class="small-inputs-container common-border-shadow">
          <input placeholder="Názov firmy" v-model="companyName" />
          <input placeholder="Adresa / Ulica" v-model="companyStreet" />
          <input placeholder="Mesto / PSČ" v-model="companyTownZipcode" />
        </div>
        <div class="small-inputs-container common-border-shadow">
          <input placeholder="IČO" v-model="ico" />
          <input placeholder="DIČ" v-model="dic" />
          <input placeholder="IČ DPH" v-model="icDph" />
        </div>
      </div>

      <input
        class="big-input-title common-border-shadow"
        placeholder="Názov cenovej ponuky"
        v-model="title"
      />
      <textarea
        class="input-welcome-text common-border-shadow"
        placeholder="Popis cenovej ponuky"
        v-model="welcomeText"
        rows="1"
      ></textarea>

      <div class="toggles">
        <Toggle
          title="Zobraziť sumárne čísla"
          :value="showSummaryNumbers"
          :update="(value) => (showSummaryNumbers = value)"
        />

        <Toggle
          v-if="showSummaryNumbers"
          title="Pridať automatický súčet nakoniec"
          :value="addTotalEnd"
          :update="(value) => (addTotalEnd = value)"
        />
      </div>

      <div class="summaries-container" v-show="showSummaryNumbers">
        <div class="summaries">
          <div class="summary new" v-if="summaries.length < 3">
            <input
              v-model="newSummaryTitle"
              class="input-new-summary-title"
              placeholder="Nadpis"
              @keydown.prevent.enter="addNewSummary"
            />
            <div class="d-flex justify-content-center align-items-center">
              <input
                v-model="newSummaryValue"
                class="input-new-summary-value"
                placeholder="Suma"
                @keydown.prevent.enter="addNewSummary"
              />
              <!-- <span>&euro;</span> -->
            </div>
          </div>

          <div
            class="summary"
            v-for="(summary, index) in summaries"
            :key="index"
          >
            <div class="delete-btn" @click="deleteSummary(index)">
              <svg
                width="12"
                height="16"
                viewBox="0 0 12 16"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M3.33301 3.99998V2.66665C3.33301 2.31302 3.47348 1.97389 3.72353 1.72384C3.97358 1.47379 4.31272 1.33331 4.66634 1.33331H7.33301C7.68663 1.33331 8.02577 1.47379 8.27582 1.72384C8.52587 1.97389 8.66634 2.31302 8.66634 2.66665V3.99998M10.6663 3.99998V13.3333C10.6663 13.6869 10.5259 14.0261 10.2758 14.2761C10.0258 14.5262 9.68663 14.6666 9.33301 14.6666H2.66634C2.31272 14.6666 1.97358 14.5262 1.72353 14.2761C1.47348 14.0261 1.33301 13.6869 1.33301 13.3333V3.99998H10.6663Z"
                  stroke="#9598A7"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </svg>
            </div>
            <Editable
              v-model="summary.title"
              :class="`summary-title editable ${summary.title ? '' : 'empty'}`"
              placeholder="Nadpis"
            />
            <div class="d-flex">
              <Editable
                v-model="summary.value"
                :class="`summary-amount editable ${
                  summary.value ? '' : 'empty'
                }`"
                placeholder="Suma"
              />

              <span v-show="summary.value"></span>
            </div>
            <!-- <p class="summary-title">{{ summary.title }}</p>
            <p class="summary-amount">{{ summary.value }} &euro;</p> -->
          </div>
          <div class="summary total" v-if="addTotalEnd">
            <p class="summary-title">Total</p>
            <p class="summary-amount">
              {{ summariesTotal }} {{ currencySymbol }}
            </p>
          </div>
        </div>
        <div
          :class="`common-border-shadow add-new-summary-btn ${
            summaries.length < 2 ? '' : 'disabled'
          }`"
          @click="addNewSummary"
        >
          + Pridať sumárne číslo
          <span v-if="summaries.length < 2"> {{ summaries.length + 2 }}/3</span>
        </div>
      </div>
      <div class="items-container">
        <Draggable v-model="items" :handle="'.drag-area'">
          <transition-group>
            <Item
              v-for="(item, index) in items"
              :key="`parent-${index}`"
              :remoteItem="item"
              :index="index"
              :deleteItem="deleteItem"
              :moveUp="moveUp"
              :moveDown="moveDown"
              :totalLength="items.length"
              :addSubItem="addSubItem"
              :currency="currency"
              :currencySymbol="currencySymbol"
              :titleOptions="titleOptions"
              :setTitleOptions="setTitleOptions"
              :selectItem="selectItem"
              :historyOfItems="historyOfItems"
            />
          </transition-group>
        </Draggable>

        <div
          @click="addNewItem"
          class="blue-button large"
          style="margin-top: 15px"
        >
          + Pridať novu položku
        </div>
      </div>

      <div
        class="my-container common-border-shadow template-use file-upload"
        @click="showFileUpload = !showFileUpload"
      >
        <div class="select-template-buttons">
          <div style="text-align: left; cursor: pointer">
            <p class="template-use-title">Súbory na stiahnutie</p>
            <p class="template-use-desc">
              Nahraj dokumenty, ktoré majú byť k dispozícií na stiahnutie.
            </p>
          </div>
          <div>
            <img
              :src="arrowDown"
              :class="`arrow-down-icon ${showFileUpload ? 'reversed' : ''}`"
            />
          </div>
        </div>
        <div
          class="form-floating type-item template-use-select"
          v-show="showFileUpload"
          @click.stop
        >
          <div class="upload-files-cloud" @click="triggerFileUpload">
            <svg
              width="29"
              height="27"
              viewBox="0 0 29 27"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
              xmlns:xlink="http://www.w3.org/1999/xlink"
            >
              <rect width="29" height="27" fill="url(#pattern0)" />
              <defs>
                <pattern
                  id="pattern0"
                  patternContentUnits="objectBoundingBox"
                  width="1"
                  height="1"
                >
                  <use
                    xlink:href="#image0_1_1477"
                    transform="translate(-0.767979 -0.401506) scale(0.00672384 0.0074954)"
                  />
                </pattern>
                <image
                  id="image0_1_1477"
                  width="364"
                  height="226"
                  xlink:href="data:image/png;base64,"
                />
              </defs>
            </svg>
            <span class="text">
              Vlož <b>súbor</b> alebo presunutím
              <span class="green-underline">nahraj súbor</span>
            </span>
          </div>
          <div :class="`file-outer`">
            <input type="file" name="files" :class="`files`" />
          </div>
        </div>
      </div>

      <div style="width: 100%">
        <div class="blue-container totals">
          <div class="totals-toggles">
            <div class="total-toggles-container">
              <Toggle
                :value="totalDiscount"
                :update="(value) => (totalDiscount = value)"
                title="Pridať celkovú zľavu"
              />
              <input
                class="simple-bottom-border-input"
                v-model="totalDiscountValue"
              />
              %
            </div>

            <div class="total-toggles-container">
              <Toggle
                :value="vatEnable"
                :update="(value) => (vatEnable = value)"
                title="Nastaviť cenu s"
              />
              <input
                class="simple-bottom-border-input"
                v-model="vatValue"
              /><span>% DPH</span>
            </div>
          </div>

          <div class="total-without-vat top-price" v-if="totalDiscount">
            <span>Cena spolu:</span>
            <del>
              <div class="red-badge">Zľava {{ totalDiscountValue }}%</div>
              {{ total.from }} {{ currencySymbol }}
              <span v-if="total.to !== total.from">
                - {{ total.to }} {{ currencySymbol }}
              </span>
            </del>
          </div>

          <div class="total-with-vat" v-if="vatEnable">
            <span>Cena s DPH ({{ vatValue }}%)</span>
            <span class="price">
              {{ totalWith21VAT.from }} {{ currencySymbol }}
              <span v-if="totalWith21VAT.to !== totalWith21VAT.from">
                - {{ totalWith21VAT.to }} {{ currencySymbol }}
              </span>
            </span>
          </div>
          <div class="total-without-vat">
            <span>
              Cena
              <span v-if="vatEnable">bez DPH</span>
              <span v-if="totalDiscount"> po zľave </span>
            </span>
            <span class="price">
              <span v-if="total.sum || total.sum === 0">
                <span
                  v-if="totalDiscount && parseFloat(totalDiscountValue) > 0"
                >
                  {{ totalWithDiscount.sum }} {{ currencySymbol }}
                </span>
                <span v-else>{{ total.sum }} {{ currencySymbol }}</span>
              </span>
              <span v-else>
                <span
                  v-if="totalDiscount && parseFloat(totalDiscountValue) > 0"
                >
                  {{ totalWithDiscount.from }} {{ currencySymbol }} -
                  {{ totalWithDiscount.to }} {{ currencySymbol }}
                </span>
                <span v-else>
                  {{ total.from }} {{ currencySymbol }} - {{ total.to }}
                  {{ currencySymbol }}
                </span>
              </span>
            </span>
          </div>
        </div>
      </div>
    </div>

    <div class="my-container common-border-shadow offer">
      <div class="offer-footer">
        <div class="left">
          <textarea
            class="box common-border-shadow info"
            rows="5"
            v-model="leftTextArea"
          >
          </textarea>
          <!-- <p class="info">{{ companyName }}</p>
            <p class="info">{{ companyStreet }}</p>
            <p class="info">{{ companyTownZipcode }}</p>
            <p class="info">IČO: {{ ico }}</p>
            <p class="info">DIČ: {{ dic }}</p>
            <p class="info">IČ DPH:{{ icDph }}</p> -->
        </div>
        <div class="middle">
          <div class="middle-text">platnosť cenovej ponuky do 21. 10. 2020</div>
          <img src="/offer-footer.png" />
        </div>
        <div class="right">
          <textarea
            class="box common-border-shadow stacked info"
            rows="2"
            v-model="rightTextArea"
          >
          </textarea>
          <!-- <p class="info">info@matate.sk</p>
          <p class="info">+421 944 439 749</p>
          <p class="info">www.matate.sk</p> -->
          <div class="box common-border-shadow stacked">
            <div style="width: 100%">
              <img
                :src="signature"
                @click="clickSignature"
                style="max-width: 100px; cursor: pointer"
              />
              <label class="logo-upload-text" v-show="!signature">
                <span> Zmeniť podpis </span>
                <input
                  class="signature-input"
                  type="file"
                  placeholder="Change signature"
                  @change="signatureInput"
                />
              </label>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="footer-buttons">
      <div class="left">
        <div class="simple-button">Späť</div>
        <div class="simple-text">
          Automaticky uložené <span class="gray-text">11:40:42</span>
        </div>
      </div>
      <div class="right">
        <div class="green-button">Uložiť</div>
        <div class="green-button">Uložiť ako šablónu</div>
        <button
          type="submit"
          value="shit"
          class="green-button filled"
          @click="finish"
        >
          Publikovať ponuku
        </button>
      </div>
    </div>
    <div class="final-buttons">
      <div class="gray-button">
        <svg
          width="14"
          height="14"
          viewBox="0 0 14 14"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M8 6.641L9.121 5.521C9.30947 5.3387 9.562 5.23773 9.82421 5.23982C10.0864 5.24191 10.3373 5.3469 10.5228 5.53218C10.7084 5.71746 10.8137 5.9682 10.8162 6.2304C10.8186 6.49259 10.718 6.74527 10.536 6.934L7.707 9.763C7.61435 9.85618 7.50419 9.93013 7.38285 9.9806C7.26152 10.0311 7.13141 10.057 7 10.057C6.86859 10.057 6.73848 10.0311 6.61715 9.9806C6.49581 9.93013 6.38565 9.85618 6.293 9.763L3.464 6.934C3.37116 6.84102 3.29753 6.73067 3.24734 6.60924C3.19714 6.48781 3.17135 6.35769 3.17144 6.22629C3.17163 5.96093 3.27723 5.70651 3.465 5.519C3.65277 5.33149 3.90734 5.22626 4.17271 5.22644C4.43807 5.22663 4.69249 5.33223 4.88 5.52L6 6.641V1C6 0.734784 6.10536 0.48043 6.29289 0.292893C6.48043 0.105357 6.73478 0 7 0C7.26522 0 7.51957 0.105357 7.70711 0.292893C7.89464 0.48043 8 0.734784 8 1V6.641ZM1 12H13C13.2652 12 13.5196 12.1054 13.7071 12.2929C13.8946 12.4804 14 12.7348 14 13C14 13.2652 13.8946 13.5196 13.7071 13.7071C13.5196 13.8946 13.2652 14 13 14H1C0.734784 14 0.48043 13.8946 0.292893 13.7071C0.105357 13.5196 0 13.2652 0 13C0 12.7348 0.105357 12.4804 0.292893 12.2929C0.48043 12.1054 0.734784 12 1 12Z"
            fill="#212541"
          />
        </svg>
        Stiahnuť PDF
        <img
          :src="i"
          class="i-icon"
          data-bs-toggle="tooltip"
          data-bs-placement="top"
          title="tooltip on top"
        />
      </div>
      <div class="gray-button">
        <img :src="eye" class="eye-icon" />
        Pozrieť ponuku online
        <img
          :src="i"
          class="i-icon"
          data-bs-toggle="tooltip"
          data-bs-placement="top"
          title="tooltip on top"
        />
      </div>
    </div>
  </form>
</template>

<script>
import Draggable from "vuedraggable";

import top from "@/assets/svg/top.svg";
import i from "@/assets/svg/i.svg";
import eye from "@/assets/svg/eye.svg";
import searchIcon from "@/assets/svg/search.svg";
import arrowDown from "@/assets/svg/arrow-down.svg";
import { fileUploaderOptionsThumbnail } from "@/assets/constants.js";

import Editable from "./components/Editable.vue";
import Toggle from "./components/Toggle.vue";
import Item from "./components/Item.vue";

export default {
  name: "App",
  components: { Toggle, Item, Editable, Draggable },
  data() {
    return {
      // *****
      // START TO SEND, THIS DATA BELOW NEEDS TO BE SENT TO THE API

      // quote settings
      internalQuoteName: "",
      internalQuoteId: "",
      language: { label: "Slovenčina", value: "sk", flag: "sk.svg" },
      currency: "EUR",
      nameOfClient: "",
      color: "#F3385A",
      deliveryDateTitle: "",
      deliveryDateText: "",
      validityDate: null,
      hideDofferoLogo: false,

      //template select
      templateSelect: "Vianočná ponuka",
      // top inputs
      title: "",
      welcomeText: "",
      companyName: "",
      companyStreet: "",
      companyTownZipcode: "",
      ico: "",
      dic: "",
      icDph: "",
      approveDeclinePriceQuote: false,
      addDeliveryDate: false,
      showReviews: false,
      logo: null,
      // summaries
      summaries: [],
      newSummaryTitle: "",
      newSummaryValue: "",
      //bottom
      leftTextArea: "",
      rightTextArea: "",
      signature: null,
      // items
      items: [],
      // global discounts and vat
      totalDiscountValue: 15,
      vatValue: 21,

      // END TO SEND
      // ********

      // options for selects
      defaultColors: [
        "#F3385A",
        "#FF551F",
        "#FFE299",
        "#6CC65E",
        "#A4E6F4",
        "#3960C6",
        "#BD00FF",
        "#A40000",
        "#B3B3B3",
        "#000000",
      ],
      selectOptions: ["Vianočná ponuka", "awef", "awef"],
      languageOptions: [
        { label: "Slovenčina", value: "sk", flag: "sk.svg" },
        { label: "English", value: "en", flag: "en_GB.svg" },
        { label: "Greek", value: "el", flag: "el.svg" },
      ],
      currencyOptions: ["USD", "GBP", "EUR", "CZK"],
      clients: ["client 1", "client 2", "good client"],

      // toggles
      showFileUpload: false,
      showTemplateSelect: false,
      drag: false,
      showSummaryNumbers: false,
      addTotalEnd: false,
      totalDiscount: false,
      vatEnable: false,

      // icons
      top,
      i,
      eye,
      searchIcon,
      arrowDown,

      // title options
      titleOptions: [
        {
          group: "Database of items",
          items: [
            {
              id: 1,
              title: "Title of item",
              richText: "Lorem Ipsum dolor sit",
              value: 800,
              images: [],
              type: {
                value: 1,
                label: "Cena za hodinovú sadzbu",
              },
              rate: 10,
              hours: 5,
              note: "note",
              hasDiscount: false,
              totalWithSubItems: 0,
              totalWithSubitemsWithDiscount: 0,
              total: 50,
              subItems: [],
              timestamp: null,
              types: [
                {
                  value: 1,
                  label: "Cena za hodinovú sadzbu",
                },
                {
                  value: 2,
                  label: "Cenový rozsah od - do",
                },
                {
                  value: 3,
                  label: "Hodinový rozsah od -do",
                },
                {
                  value: 4,
                  label: "Vlastná cena",
                },
                {
                  value: 5,
                  label: "Celková cena z podpoložiek",
                },
              ],
              priceFrom: 0,
              priceTo: 0,
              hoursFrom: 0,
              hoursTo: 0,
              count: 0,
              unit: {
                value: 1,
                label: "ks",
              },
              discount: 0,
              //title
              //description (richText)
              //images
              //hoursFrom , hoursTo,
              //priceFrom, priceTo,
              //rate, hours,
              //count, value
              //unit
              //total
              //timestamp,
              //note
              //discount, hasDiscount
            },
          ],
        },
      ],
      historyOfItems: [1],

      // init item data
      initItem: {
        title: "",
        id: -1,
        type: {
          value: 1,
          label: "Cena za hodinovú sadzbu",
        },
        images: null,
        types: [
          {
            value: 1,
            label: "Cena za hodinovú sadzbu",
          },
          {
            value: 2,
            label: "Cenový rozsah od - do",
          },
          {
            value: 3,
            label: "Hodinový rozsah od -do",
          },
          {
            value: 4,
            label: "Vlastná cena",
          },
          {
            value: 5,
            label: "Celková cena z podpoložiek",
          },
        ],
        richText: "",
        hoursFrom: 0,
        imagesUploadedLength: 0,
        hoursTo: 0,
        priceFrom: 0,
        priceTo: 0,
        rate: 0,
        hours: 0,
        count: 0,
        value: 0,
        unit: null,
        subItems: [],
        total: 0,
        totalWithSubitems: 0,
        totalWithSubitemsWithDiscount: 0,
        timestamp: null,
        note: null,
        hasDiscount: false,
        discount: 0, // percent
        doNotCountInTotal: false,
      },
    };
  },
  computed: {
    currencySymbol() {
      let currency;
      switch (this.currency) {
        case "GBP":
          currency = "£";
          break;
        case "USD":
          currency = "$";
          break;
        case "CZK":
          currency = "Kč";
          break;
        case "EUR":
        default:
          currency = "€";
      }

      return currency;
    },
    summariesTotal() {
      let total = 0;
      this.summaries.forEach(({ value }) => {
        const valueNumber = parseFloat(value);

        if (!isNaN(valueNumber)) total += valueNumber;
      });

      if (this.newSummaryValue) {
        const newSummaryValueNumber = parseFloat(this.newSummaryValue);

        if (!isNaN(newSummaryValueNumber)) total += newSummaryValueNumber;
      }

      return isNaN(total) ? 0 : total;
    },
    total() {
      let finalTotal = { from: 0, to: 0 };
      this.items.forEach((item) => {
        if (!item.doNotCountInTotal) {
          if (
            item.totalWithSubitemsWithDiscount.from ||
            item.totalWithSubitemsWithDiscount.from === 0
          )
            finalTotal.from += parseFloat(
              item.totalWithSubitemsWithDiscount.from
            );
          if (
            item.totalWithSubitemsWithDiscount.to ||
            item.totalWithSubitemsWithDiscount.to === 0
          )
            finalTotal.to += parseFloat(item.totalWithSubitemsWithDiscount.to);
          else if (typeof item.totalWithSubitemsWithDiscount === "number") {
            finalTotal.from += parseFloat(item.totalWithSubitemsWithDiscount);
            finalTotal.to += parseFloat(item.totalWithSubitemsWithDiscount);
          }
        }
      });
      if (finalTotal.from === finalTotal.to) finalTotal.sum = finalTotal.from;
      return finalTotal;
    },
    totalWithDiscount() {
      const percentage = (100 - parseFloat(this.totalDiscountValue)) / 100;

      return {
        from: this.totalDiscount
          ? (this.total.from * percentage)?.toFixed(2)
          : this.total.from?.toFixed(2),
        to: this.totalDiscount
          ? (this.total.to * percentage)?.toFixed(2)
          : this.total.to?.toFixed(2),
        sum: this.totalDiscount
          ? (this.total.sum * percentage)?.toFixed(2)
          : this.total.sum?.toFixed(2),
      };
    },
    totalWith21VAT() {
      const total = this.totalWithDiscount;

      const percentage = (100 + parseFloat(this.vatValue)) / 100;

      return {
        from: parseFloat((total.from * percentage).toFixed(2)),
        to: parseFloat((total.to * percentage).toFixed(2)),
      };
    },
    filteredClients() {
      const nameOfClient = this.nameOfClient?.toLowerCase();

      if (this.clients.includes(nameOfClient)) return [];

      if (nameOfClient.length <= 3) return [];
      else
        return this.clients.filter((client) =>
          client?.includes(nameOfClient?.toLowerCase())
        );
    },
    newItemId() {
      let id = -1;
      this.titleOptions?.[0]?.items?.forEach((item) => {
        if (item.id > id) {
          id = item.id;
        }
      });

      return id + 1;
    },
  },
  methods: {
    setTitleOptions(item, index, subItem, index2) {
      if (subItem) {
        console.log("adding new item in subitem", item, index, subItem, index2);

        if (!subItem.id || subItem.id < 0) {
          console.log("adding new item SUB");
          const newId = this.newItemId;
          console.log("newid is :", newId);
          subItem = {
            ...subItem,
            id: newId,
          };

          this.titleOptions[0].items.push(subItem);
          this.items[index].subItems.splice(index2, 1, subItem);
          this.historyOfItems = this.historyOfItems.filter(
            (item) => item.id === newId
          );
          this.historyOfItems.push(newId);
          return subItem.id;
        } else {
          console.log("updatign existing item SUB");
          const existingItemIndex = this.titleOptions[0].items?.findIndex(
            (itm) => itm.id === subItem.id
          );
          this.items[index].subItems.splice(index2, 1, subItem);
          if (existingItemIndex >= 0)
            this.titleOptions[0].items.splice(existingItemIndex, 1, subItem);
          else {
            console.log("existing item not found!");
          }
        }

        return;
      }

      console.log("setTitleOptions index: ", index);
      if (!item.id || item.id < 0) {
        console.log("adding new item");
        const newId = this.newItemId;
        console.log("newid is :", newId);
        item = {
          ...item,
          id: newId,
        };
        console.log("new item id is: ", item.id);
        this.titleOptions[0].items.push(item);
        this.items.splice(index, 1, item);
        this.historyOfItems = this.historyOfItems.filter(
          (item) => item.id != newId
        );
        this.historyOfItems.push(newId);
        return item.id;
      } else {
        console.log("updatign existing item");
        const existingItemIndex = this.titleOptions[0].items?.findIndex(
          (itm) => itm.id === item.id
        );
        this.items.splice(index, 1, item);
        if (existingItemIndex >= 0)
          this.titleOptions[0].items.splice(existingItemIndex, 1, item);
        else {
          console.log("existing item not found!");
        }
      }
    },
    // selectItem(selectedItem, id) {
    selectItem(selectedItem, index, subIndex) {
      console.log(
        "selectitem:",
        selectedItem,
        ", id:",
        selectedItem,
        ", index: ",
        index,
        ", subIndex: ",
        subIndex
      );
      // const itemIndex = this.items?.findIndex((item) => item.id === id);
      // console.log("found item index: ", itemIndex);
      // if (itemIndex >= 0) this.items.splice(itemIndex, 1, selectedItem);
      this.historyOfItems = this.historyOfItems?.filter((item) => {
        // console.log(item, selectedItem.id, item == selectedItem.id);
        return item != selectedItem.id;
      });

      this.historyOfItems.push(parseInt(selectedItem.id));

      if (!subIndex && subIndex !== 0) {
        console.log("selectitem:", selectedItem, ", index:", index);
        const deletedItem = this.items.splice(
          index,
          1,
          JSON.parse(JSON.stringify(selectedItem))
        );
        console.log("deleted item: ", deletedItem);
      } else {
        console.log(
          "selectitem SUB:",
          selectedItem,
          ", index:",
          index,
          " subIndex: ",
          subIndex
        );

        const deletedItem = this.items[index].subItems.splice(
          subIndex,
          1,
          JSON.parse(JSON.stringify(selectedItem))
        );
        console.log("deleted item SUB: ", deletedItem);
      }
    },
    enableFileUpload() {
      window.$(`.file-upload input.files`).fileuploader({
        theme: "default",
        limit: 10,
        thumbnails: fileUploaderOptionsThumbnail,
        extensions: ["jpg", "jpeg", "png", "bmp", "gif", "svg"],
        // Callback fired on selecting and processing a file
        onSelect: () => {
          const input = document.querySelector(`.file-upload input.files`);

          this.images = input.files;
          console.log(this.images);
          this.imagesUploadedLength = input?.files?.length;
        },
        onRemove: () => {
          this.imagesUploadedLength -= 1;
        },
      });

      this.removeFileUploaderText();
      setTimeout(() => {
        this.removeFileUploaderText();
      }, 300);
    },
    removeFileUploaderText() {
      // remove text
      const fileUploader = document.querySelector(`.file-upload .fileuploader`);
      if (fileUploader) {
        // console.log(fileUploader.nextSibling);
        fileUploader?.nextSibling?.remove();
      }
    },
    triggerFileUpload() {
      const input = document.querySelector(
        `.file-upload .file-outer .fileuploader-input-caption`
      );
      // console.log(input);
      input?.click();
    },
    addClient(name) {
      this.clients.push(name);
    },
    selectNameClient(client) {
      this.nameOfClient = client;
    },
    finish() {
      console.log(this);
    },
    clickCustomColor() {
      console.log(
        "length",
        this.defaultColors.filter((col) => col === this.color).length
      );

      if (this.defaultColors.filter((col) => col === this.color).length > 0) {
        this.color = "#eeeeee";
        setTimeout(() => document.querySelector(".color-input")?.click(), 300);
      }
    },
    deleteItem(index, itemsContainer = this.items) {
      itemsContainer.splice(index, 1);
    },
    logoInput(e) {
      const input = e.target;

      const file = input?.files?.[0];
      if (file) this.logo = URL.createObjectURL(file);
    },
    signatureInput(e) {
      const input = e.target;

      const file = input?.files?.[0];
      if (file) this.signature = URL.createObjectURL(file);
    },
    clickSignature() {
      const signatureInput = document.querySelector(".signature-input");
      console.log;
      if (signatureInput) signatureInput.click();
    },
    addNewSummary() {
      if (this.summaries.length >= 2) return;

      this.summaries.push({
        title: this.newSummaryTitle,
        value: this.newSummaryValue,
      });

      this.clearSummaryInputs();
    },
    deleteSummary(index) {
      console.log("deletesummary: ", index);
      this.summaries.splice(index, 1);
    },
    clearSummaryInputs() {
      this.newSummaryTitle = "";
      this.newSummaryValue = "";
    },
    addNewItem() {
      this.items.push(
        JSON.parse(JSON.stringify({ ...this.initItem, timestamp: Date.now() }))
      );
    },
    moveItem(from, to, itemsContainer) {
      // remove `from` item and store it
      var f = itemsContainer.splice(from, 1)[0];
      // insert stored item into position `to`
      itemsContainer.splice(to, 0, f);
    },
    moveUp(index, itemsContainer = this.items) {
      this.moveItem(index, index - 1, itemsContainer);
    },
    moveDown(index, itemsContainer = this.items) {
      this.moveItem(index, index + 1, itemsContainer);
    },
    addSubItem(index) {
      this.items[index].subItems.push({
        ...this.initItem,
        subItems: null,
        timestamp: Date.now(),
      });
    },
    enableTooltips() {
      var tooltipTriggerList = [].slice.call(
        document.querySelectorAll('[data-bs-toggle="tooltip"]')
      );
      tooltipTriggerList.map(function (tooltipTriggerEl) {
        return new window.bootstrap.Tooltip(tooltipTriggerEl);
      });
    },
  },
  mounted() {
    this.addNewItem();

    this.enableTooltips();
    this.enableFileUpload();
  },
};
</script>

<style lang="scss">
$green: #2ed8b6;
$black: #212541;

@font-face {
  font-family: "Mont";
  src: url("~@/assets/fonts/Mont-SemiBold.otf") format("opentype");
}

@font-face {
  font-family: "Mont";
  font-weight: bold;
  src: url("~@/assets/fonts/Mont-Bold.otf") format("opentype");
}

@font-face {
  font-family: "Mont";
  font-weight: 400;
  src: url("~@/assets/fonts/Mont-Light.otf") format("opentype");
}

.eye-icon {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.bs-tooltip-auto[data-popper-placement^="top"] .tooltip-arrow::before,
.bs-tooltip-top .tooltip-arrow::before {
  border-top-color: #f3385a;
}

.bs-tooltip-auto[data-popper-placement^="left"] .tooltip-arrow::before,
.bs-tooltip-start .tooltip-arrow::before {
  border-left-color: #f3385a;
}

.editable {
  &.empty {
    border-bottom: 1px solid #dde3e8;
  }
}

.delivery-date {
  outline: none;

  &.title {
    padding: 20px 15px !important;
    margin-top: 10px !important;
  }

  &.text {
    padding: 20px 15px !important;
    margin-top: 15px !important;
  }
}

.big-input-title,
.input-welcome-text {
  &:focus {
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.09) !important;
    border-color: #90c1ea !important;
    outline-color: #90c1ea !important;
  }
}

label.form-check-label {
  user-select: none;
}

.option-container {
  display: flex;

  &.flag {
    span {
      max-width: 30px;
    }
  }
}

.multiselect__option {
  display: flex !important;
  align-items: center;

  .flag {
    svg {
      width: 30px;
      margin-right: 10px;
    }
  }
}

.name-options {
  background: #fff;
  border: 1px solid #dde3e8;
  border-top: 0;
  box-shadow: 0 2px 8px rgb(0 0 0 / 10%);
  box-sizing: border-box;
  display: grid;
  height: auto;
  left: 0;
  pointer-events: all;
  position: absolute;
  top: 58px;
  width: auto;
  z-index: 10;

  &.hidden {
    display: none;
  }

  .no-client-found {
    color: #000;
    font-family: Roboto;
    font-size: 13px;
    font-style: italic;
    font-weight: 400;
    line-height: 15px;
    padding: 22px;
    text-align: left;
  }

  .single-name-client {
    color: #212541;
    cursor: pointer;
    font-family: Roboto;
    font-size: 13px;
    font-style: normal;
    font-weight: 500;
    line-height: 15px;
    padding: 13px;
    text-align: left;

    &:hover,
    .selected {
      color: #2ed8b6;
    }
  }

  .bottom {
    border-top: 1px solid #9598a7;
    color: #2ed8b6;
    cursor: pointer;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    padding: 22px;
  }
}

.form-floating {
  margin-bottom: 0 !important;

  &.client-name {
  }

  &.title {
    .multiselect__select {
      display: none;
    }

    &.empty {
      .multiselect {
        .multiselect__content-wrapper {
          display: none;
        }
      }
    }
  }

  &.type-item {
    width: 230px;
    min-width: 230px;
    max-width: 230px;

    .multiselect__tags {
      min-width: 0;
      width: 100%;
    }
  }

  .multiselect__tags {
    border-radius: 0 !important;
  }

  &.rate,
  &.hours,
  &.price,
  &.select,
  &.pocet {
    max-width: 130px;
  }

  input,
  select {
    border-radius: 0 !important;
  }
}

.item {
  .input-number-container {
    + .input-number-container {
      .form-floating {
        input,
        select {
          border-left: none !important;
        }
      }
    }
  }

  .form-floating {
    + .form-floating {
      .multiselect__tags {
        border-left: none !important;
      }
    }

    + .input-number-container {
      .form-floating {
        input,
        select {
          border-left: none !important;
        }
      }
    }
  }
}

.select-template-buttons {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.multiselect__single {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: $black;
  text-transform: none;
  margin-top: 8px;
  background: transparent !important;
}

.option__image {
  width: 23px;
  margin-right: 10px;
  border-radius: 5px;
  border-color: transparent;
}

.option__label {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;

  color: $black;
}

.tooltip-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  background: #f3385a;
  box-shadow: 0px 2px 2px rgb(143 153 179 / 20%);
  border-radius: 6px;
  font-family: "Quicksand";
  font-weight: 800;
  font-size: 14px;
  line-height: 18px;
  color: #fff;
  padding: 10px;
  z-index: 10000;
  transition: opacity 0.5s ease-in-out;
}

.green-underline {
  text-decoration-line: underline;
  cursor: pointer;
  color: #2ed8b6;
}

#quote-creation {
  font-family: "Mont";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: $black;
  background: #fff;
  margin-top: 60px;
  margin: 0 auto;
  width: 100%;
  padding: 30px;
  max-width: 1075px;

  .input-box {
    position: relative;
  }

  .multiselect__option--highlight {
    background: #eee;
  }

  span.multiselect__option {
    font-weight: 400;
    font-family: "Mont";
    color: $black;
    font-size: 13px;
  }

  input[type="date"]::-webkit-inner-spin-button,
  input[type="date"]::-webkit-calendar-picker-indicator {
    opacity: 0;
    -webkit-appearance: none;
  }

  input[type="date"]::-webkit-calendar-picker-indicator {
    background: transparent;
    bottom: 0;
    color: transparent;
    cursor: pointer;
    height: auto;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: auto;
  }

  .gray-text {
    color: #dde3e8;
  }

  .simple-text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: $black;
  }

  .final-buttons {
    display: flex;
    justify-content: flex-end;
    width: 100%;

    .gray-button {
      + .gray-button {
        margin-left: 20px;
      }
    }
  }

  .footer-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;

    .right {
      display: flex;
    }

    .left {
      display: flex;
      align-items: center;

      .simple-button + .simple-text {
        margin-left: 30px;
      }
    }
  }

  .info {
    font-family: "Mont";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 182.5%;
    color: $black;
    text-align: left;
  }

  .my-container {
    &.offer {
      margin-top: 30px;
    }
  }

  .offer-footer {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    textarea:focus {
      box-shadow: 0px 2px 2px rgb(0 0 0 / 9%) !important;
      border-color: #90c1ea !important;
      outline: none;
    }

    .box {
      padding: 10px;
      min-width: 200px;

      .signature-input {
        display: none;
      }

      &.stacked {
        margin-bottom: 10px;
        + .stacked {
          margin-top: 10px;
        }
      }
    }

    .left {
    }

    .middle {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      .middle-text {
        font-family: "Mont";
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 182.5%;
        color: $black;
      }
    }

    .right {
    }
  }

  .i-icon {
    color: #9598a7;
    width: 18px;
    height: 18px;
    cursor: pointer;

    &.no-circle {
      border: none;
    }
  }

  .do-not-count-in-total-toggle {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: $black;
    justify-content: center;
    display: flex;
    position: relative;
    align-items: center;

    &.root-toggle-count-total {
      width: 100%;
      display: flex;
      justify-content: flex-end;
    }

    .i-icon {
      margin-left: 10px;
    }
  }

  .form-floating {
    font-family: "Mont";
    font-style: normal;
    font-weight: 700;
    font-size: 10px;
    line-height: 182.5%;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: #a6a8b3;
    flex-grow: 1;

    label {
      text-transform: uppercase;
    }
  }

  .items-container {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
  }

  input {
    outline: none;
    margin-top: 0;
  }

  .flex-container {
    display: flex;
    align-items: center;
  }

  .accordion-header {
    &.my-blue {
      color: white;
    }
  }

  .accordion-body {
    .my-container {
      padding: 0;
      gap: 0;
      border-radius: 0;
    }
  }

  .accordion-item {
    &.my-blue {
      .accordion-button {
        background: $black;
        outline: none;
        box-shadow: none;
        color: white;
        font-family: "Mont";
        font-style: normal;
        font-weight: 800;
        font-size: 20px;
        line-height: 182.5%;

        svg {
          margin-left: 10px;
          margin-right: 10px;
        }

        &:after {
          transform: none;
          background-image: url('data:image/svg+xml,<svg width="8" height="7" viewBox="0 0 12 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M10.3333 1L5.66659 5.66667L0.999919 1" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/></svg>');
        }

        &:not(.collapsed):after {
          background-image: url('data:image/svg+xml,<svg width="12" height="7" viewBox="0 0 12 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M10.3334 6L5.66671 1.33333L1.00004 6" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/></svg>');
        }
      }
    }
  }

  .blue-container {
    padding: 20px 40px;
    background: $black;
    border-radius: 4px;

    text-align: left;
    color: white;

    .price {
      white-space: nowrap;
    }

    .blue-container-title {
      font-family: "Mont";
      font-style: normal;
      font-weight: 800;
      font-size: 20px;
      line-height: 182.5%;
      margin-left: 10px;
    }

    &.totals {
      max-width: 580px;
      float: right;

      .total-without-vat,
      .total-with-vat {
        display: flex;
        justify-content: space-between;

        font-family: "Mont";
        font-style: normal;
        font-weight: 800;
        line-height: 182.5%;
        color: white;
        margin: 10px 0;
      }

      .total-without-vat {
        font-size: 14px;

        .price {
          font-size: 24px;
        }
      }

      .top-price {
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid white;
        font-size: 18px;

        del {
          position: relative;
          font-family: "Mont";
          font-style: normal;
          font-weight: 800;
          font-size: 18px;
          line-height: 182.5%;
        }

        .red-badge {
          background: #db355f;
          padding: 2px 5px;
          position: absolute;
          top: 5px;
          bottom: 0;
          left: -80px;
          font-family: "Mont";
          font-style: normal;
          font-weight: 800;
          font-size: 12px;
          line-height: 18px;
          height: 20px;
          border-radius: 2px;
        }
      }

      .total-with-vat {
        font-size: 18px;

        .price {
          font-size: 34px;
        }
      }
    }

    .totals-toggles {
      display: flex;
      text-align: left;
      margin-bottom: 10px;

      .toggle {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;

        &:first-of-type {
          .switch {
            margin-left: 0;
          }
        }
      }
    }
  }

  .common-border-shadow {
    background: #ffffff;
    border: 1px solid #dadada;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
  }

  .no-top-border-radius {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }

  .no-bottom-border-radius {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  .my-container {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 32px 40px;
    gap: 16px;
  }

  .file-upload {
    width: 100%;

    * {
      text-transform: none !important;
    }
  }

  .arrow-down-icon {
    padding: 20px;
    cursor: pointer;
    transition: all 0.2s;

    &.reversed {
      transform: rotate(180deg);
    }
  }

  .upload-files-cloud {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 15px;
    background: #ffffff;
    border: 1px dashed #e1e2e3;
    border-radius: 10px;
    cursor: pointer;
    user-select: none;

    span.text {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;

      color: #c2c3c9;
    }
  }

  .template-use {
    margin: 20px 0;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }

  .template-use-select {
    width: 100%;
    margin-top: 10px;
    max-width: none;
    min-width: 0;
  }

  .green-button,
  .simple-button,
  .gray-button {
    user-select: none;
  }

  .green-button,
  .simple-button {
    border: 1px solid $green;
    border-radius: 4px;
    padding: 20px 32px;
    cursor: pointer;
    color: $green;
    text-align: center;
    font-weight: 600;
  }

  .simple-button {
    color: $black;
    border: 1px solid #dde3e8;
  }

  .gray-button {
    background: #f7f8f8;
    border-radius: 4px;
    padding: 20px 32px;
    font-family: "Quicksand";
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    color: $black;
    cursor: pointer;

    svg {
      margin-right: 10px;
    }

    .i-icon {
      margin-left: 10px;
    }
  }

  .green-button {
    border: 1px solid $green;
    color: $green;

    &.filled {
      background: $green;
      color: white;
    }

    + .green-button {
      margin-left: 20px;
    }
  }

  .template-use-title {
    font-family: "Mont";
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 182.5%;
    color: $black;
  }

  .template-use-desc {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: $black;
  }

  .big-input-title {
    font-size: 36px;
    font-style: normal;
    font-weight: 800;
    font-size: 36px;
    line-height: 77px;
    width: 100%;
    color: $black;
    padding: 10px 30px;
  }

  .input-welcome-text {
    color: $black;
    width: 100%;
    padding: 11px;
  }

  .top-inputs {
    display: flex;
    width: 100%;
  }

  .logo-upload-text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-align: right;
    text-decoration-line: underline;
    cursor: pointer;
    flex-shrink: 1;
    padding-right: 15px;

    color: #2ed8b6;

    input {
      display: none;
    }
  }

  .logo-input-container {
    flex: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;

    img {
      max-width: 200px;
      max-height: 100px;
    }

    .logo-input {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 16px;
      text-align: right;
      text-decoration-line: underline;

      color: $green;
    }
  }

  .small-inputs-container {
    flex: 1;
    margin-left: 10px;
    padding: 15px;

    input {
      font-family: "Mont";
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 18px;
      /* identical to box height */
      outline: none;

      display: flex;
      align-items: center;

      color: rgba(33, 37, 65, 0.5);

      width: 100%;
      padding-bottom: 5px;
      margin: 10px 0;
      border-bottom: 1px solid #dde3e8;

      &:focus {
        border-bottom-color: #9fa4a7;
      }
    }
  }

  .toggles {
    display: flex;
    justify-content: center;
    align-items: center;

    .toggle-with-tooltip {
      display: flex;
      align-items: center;

      .i-icon {
        margin-left: 10px;
        width: 15px;
        height: 15px;
        margin-bottom: 5px;
      }
    }
  }

  .toggle {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .input-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px 16px;
    justify-content: space-between;

    background: #ffffff;
    border: 1px solid #dde3e8;
    border-radius: 6px;
    margin-bottom: 10px;

    &:focus-within {
      box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.09) !important;
      border-color: #90c1ea !important;
    }

    label {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 700;
      font-size: 11px;
      line-height: 13px;
      text-align: left;
      /* identical to box height */

      letter-spacing: 0.1em;
      text-transform: uppercase;
      display: flex;
      flex-direction: column;
      width: 100%;

      color: #9598a7;

      input {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;

        color: $black;
        padding-top: 10px;
      }
    }
  }

  .quote-settings {
    width: 100%;
    display: flex;
    flex-direction: row;

    .quote-side {
      flex-grow: 1;
      width: 50%;
      padding: 0 15px;

      .client-boxes {
        display: flex;
        margin: 10px 0;
        flex-wrap: wrap;

        .client-box {
          margin: 5px;
          background: #f2f7fb;
          border-radius: 2px;
          font-family: "Mont";
          font-style: normal;
          font-weight: 600;
          font-size: 12px;
          line-height: 15px;
          padding: 3px 10px;
          cursor: pointer;

          color: $black;
        }
      }

      .quote-selects-flex {
        display: flex;
        margin-left: -5px;
        margin-right: -5px;

        > * {
          width: 50%;
          margin: 0 5px;
        }

        .multiselect {
          height: 60px;
        }

        .multiselect__select {
          height: 58px;
        }
      }

      .hide-logo-toggle-container {
        display: flex;
        justify-content: flex-start;
        align-items: center;
      }

      .toggles {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .toggle {
          margin-bottom: 8px;
        }
      }

      &.left {
        border-right: 2px solid #dde3e8;
      }

      &.right {
        .quote-title {
          font-family: "Roboto";
          font-style: normal;
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          color: $black;
          text-align: left;
        }

        .quote-subtitle {
          font-family: "Roboto";
          font-style: normal;
          font-weight: 400;
          font-size: 14px;
          line-height: 16px;
          text-align: left;
          margin-top: 5px;

          color: #9598a7;
        }

        .colors {
          display: flex;
          margin-top: 20px;
        }

        .cpicker {
          min-width: 200px;
          cursor: pointer;
          border: 1px solid #dde3e8;
          margin: 8px 0;
        }

        .custom-color-button {
          background: #ffffff;
          border: 1px solid #dde3e8;
          box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
          border-radius: 3px;
          padding: 10px;
          margin: 10px 0;
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;

          &.selected {
            border-color: #555;
          }
        }

        .color-input {
          height: 25px;
          width: 25px;
          margin-left: 10px;
          border: 1px solid #dde3e8;
          box-shadow: 0px 4px 10px rgb(0 0 0 / 10%);
          border-radius: 3px;
          width: 28px;
          height: 28px;
          margin: 0 5px;
          padding: 5px;
          background: white;
          cursor: pointer;
        }

        .color-box {
          border: 1px solid #dde3e8;
          box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
          border-radius: 3px;
          width: 28px;
          height: 28px;
          margin: 0 5px;
          padding: 5px;
          cursor: pointer;

          &.selected {
            border: 1px solid #555;
          }

          .inner {
            height: 100%;
            width: 100%;
          }
        }
      }
    }
  }

  .summaries-container {
    width: 100%;
  }

  .summaries {
    display: flex;
    width: 100%;
    margin-top: 10px;

    .summary {
      margin: 0 10px;
      min-width: 150px;
      position: relative;
      max-width: 200px;

      &:first-of-type {
        margin-left: 0;
      }

      &.new {
        max-width: 250px;

        input {
          max-width: 130px;
          padding-top: 2px;
          border-bottom: 1px solid #dde3e8;
          outline: none;

          &.input-new-summary-title {
            font-size: 12px;
            height: 30px;
            width: 100%;
            font-weight: 600;
          }

          &.input-new-summary-value {
            width: 100%;
            font-weight: 600;
            font-family: "Mont";
          }
        }
      }

      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 24px 28px;
      padding-top: 15px;
      border-radius: 8px;

      font-family: "Mont";
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 31px;
      flex-grow: 1;
      color: $black;
      border: 1px solid $black;

      .delete-btn {
        color: #9598a7;
        font-size: 12px;
        position: absolute;
        right: 10px;
        top: 10px;
        cursor: pointer;
      }

      .summary-title {
        font-family: "Mont";
        font-style: normal;
        font-weight: 800;
        font-size: 14px;
        line-height: 182.5%;
        width: 100%;
        text-align: left;
        outline: none;

        &.editable {
          width: auto;
          min-width: 20px;
          padding-top: 7px;
        }
      }

      .summary-amount {
        font-family: "Mont";
        font-style: normal;
        font-weight: 800;
        font-size: 24px;
        line-height: 31px;
        width: 100%;
        text-align: left;
        min-width: 50px;
        outline: none;
      }

      &.total {
        background: $black;
        color: white;
        border: none;
        max-width: 300px;
        margin-right: 0;
        padding-top: 22px;
      }
    }
  }

  .add-new-summary-btn {
    padding: 13px 30px;
    cursor: pointer;
    width: 250px;
    margin-top: 20px;
    margin-bottom: 10px;
    user-select: none;

    &.disabled {
      opacity: 0.7;
      cursor: not-allowed;
    }
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 19px;
    margin-right: 5px;
    margin-left: 10px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 15px;
    width: 15px;
    left: 3px;
    bottom: 2px;
    background-color: white;
    transition: 0.4s;
  }

  input:checked + .slider {
    background-color: $green;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px $green;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(16px);
    -ms-transform: translateX(16px);
    transform: translateX(16px);
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  .blue-button {
    &.large {
      background: $black;
      border-radius: 4px;
      font-family: "Roboto";
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      color: #ffffff;
      width: 100%;
      padding: 22px 40px;
      margin: 5px 0;
      cursor: pointer;
    }
  }

