    background: transparent;
    color: white;
    height: 25px;
    width: 25px;
  }

  .total-toggles-container {
    display: flex;
    align-items: center;

    .form-check {
      flex-grow: 1;
      margin-right: 7px;
    }

    + .total-toggles-container {
      margin-left: 20px;
    }
  }
}

